













































export default {
	props: {
		count: {
			type: Number,
			default: 2
		}
	},
	data() {
		return {
			imageList: [],
			picId:[],
			taskData:{
				taskTitle:"",
				copyWriting:"",
				contributionNo:'',
				releaseTaskNo:"",
				allMoney:"",
				picId:"",
				picId2:"",
			},
			taskIdentifier:"", //任务标号
			vipLevel:"",
			userInfo:{}
		};
	},
	onUnload() {
		this.imageList = [];
	},
	onLoad() {
		self = this;
		this.vipLevel = uni.getStorageSync('vipLevel') ?uni.getStorageSync('vipLevel') :"";
		this.userInfo = uni.getStorageSync('userInfo');
	},
	methods:{
		publishTask(){
			var that = this;
			this.taskData.allMoney = Number(this.taskData.contributionNo)*Number(this.taskData.releaseTaskNo);
			this.taskData.picId = this.picId[0];
			this.taskData.picId2 = this.picId[1];
			console.log(this.taskData,this.picId);
			this.taskData.account = this.userInfo.account;
			const jsonString = {
				taskInfo:this.taskData,
				requestType:"creatTask",
			}
			const param ={
				controllerRequestType:"taskControllerService",
				jsonString:JSON.stringify(jsonString)
			}
			uni.request({
				url:that.websiteUrl,
				method:'POST',
				header: {
					"content-type":"application/x-www-form-urlencoded"
				},
				data:param,
				success: (res) => {
					if(res.data.errorCode == '0000'){
						
					}
					if(res.data.errorCode == '9000'){
						console.log(res.data.taskInfo);
						if(res.data.taskInfo.statusTask == '04'){
							that.taskIdentifier = res.data.taskInfo.taskIdentifier;
							uni.navigateTo({
								url:'bVip',
							})
						}
						}
				},
				fail:(res) =>{
					console.log(JSON.stringify(res));
				}
			})
		},
		totalPrice(){
			var  that = this;
			that.totalPrices = that.singleMoney*that.taskNumber;
		},
		urlTobase64(url){
			var that = this;
			uni.request({
				url:url,
				method:'GET',
				responseType: 'arraybuffer',
				success: ress => {
					that.base64 = wx.arrayBufferToBase64(ress.data); //把arraybuffer转成base64 
					that.base64 = 'data:image/jpeg;base64,' + that.base64 //不加上这串字符，在页面无法显示的哦
				}
			})
		},
		chooseImg() {
			var that = this;
			//选择图片
			uni.chooseImage({
				sourceType: ['camera', 'album'],
				sizeType: 'compressed',
				count: self.count - self.imageList.length,
				success: function(res) { 
					uni.request({
						url:res.tempFilePaths[0],
						method:'GET',
						responseType: 'arraybuffer',
						success: ress => {
							var base64 = wx.arrayBufferToBase64(ress.data); //把arraybuffer转成base64 
							// base64 = 'data:image/jpeg;base64,' + base64 //不加上这串字符，在页面无法显示的哦
							// that.base64 = base64;
							const jsonString = {
								taskInfo:{
									picBast64:base64,
									upType:'pic',
								},
								requestType:"upFile",
							}
							const param ={
								controllerRequestType:"taskControllerService",
								jsonString:JSON.stringify(jsonString)
							}
							uni.request({
								url:that.websiteUrl,
								method:'POST',
								header: {
									"content-type":"application/x-www-form-urlencoded"
								},
								data:param,
								success: (res) => {
									if(res.data.errorCode == '0000'){
										that.picId.push(res.data.taskInfo.picId);
									}
								},
								fail:(res) =>{
									console.log(JSON.stringify(res));
								}
							})
							
						}
					})
					
					// that.base64 = that.urlTobase64(res.tempFilePaths[0]);
				
					// self.imageList = self.imageList.concat(res.tempFilePaths);
					//只添加不重复的文件
					if (self.imageList.length > 0) {
						for (let img of res.tempFilePaths) {
							let index = self.imageList.findIndex(item => item === img);
							if (index === -1) {
								self.imageList.push(img);
							}
						}
					} else {
						self.imageList = res.tempFilePaths;
					}
					
					// console.log(JSON.stringify(self.imageList))
					//触发所选择文件更改事件，参数为文件列表
					self.$emit('change', self.imageList);
				}
			});
		},
		removeImage(img) {
			uni.showModal({
				content: '确认删除选中的图片？',
				success: function() {
					const index = self.imageList.findIndex(item => item === img);
					self.imageList.splice(index, 1);
				}
			});
		},
		previewImage() {
			//预览图片
			uni.previewImage({
				urls: self.imageList
			});
		}
	}
}



